<template>
    <div class="changepwd">
      <mt-header title="修改密码" id="header">
        <router-link to="/" slot="left" >
          <mt-button icon="back"></mt-button>
        </router-link>
      </mt-header>
        <mt-field label="旧密码" placeholder="请输入旧密码" v-model="username"></mt-field>
        <mt-field label="新密码" placeholder="请输入新密码" type="password" v-model="oldpwd"></mt-field>
        <mt-field label="确认新密码" placeholder="请再次输入新密码" type="password" v-model="newpwd"></mt-field>
      <mt-button type="primary" class="btn" @click="change">修改密码</mt-button>
    </div>
</template>

<script>
  import axios from 'axios'
    export default {
        name: "changepwd",
      methods:{
          change(){
            let that = this
            if(this.oldpwd==this.newpwd){
              axios.post('/api/user/update', {
                pwd: this.username,
                newPwd: this.newpwd
              },{withCredentials:true})
                .then(function (response) {
                  console.log(response);
                  that.$router.push('/mydidi')
                })
                .catch(function (error) {
                  console.log(error);
                });
            }else{
              alert('您两次输入的新密码不一样，请重新出入！！！')
            }
          }
      },
      data(){
          return{
            username:'',
            oldpwd:'',
            newpwd:''
          }
      }
    }
</script>

<style scoped>
.btn{
  width:90%;
  margin: 20px auto;
  margin-left: 20px;
}
</style>



